﻿@font-face {
    font-family: 'Proxima Nova Regular';
    src: url($fontsPath + 'ProximaNova-Regular.eot');
    src: url($fontsPath + 'ProximaNova-Regular?#iefix') format('embedded-opentype'), 
        url($fontsPath + 'ProximaNova-Regular.woff2') format('woff2'), 
        url($fontsPath + 'ProximaNova-Regular.woff') format('woff'), 
        url($fontsPath + 'ProximaNova-Regular.ttf') format('truetype'), 
        url($fontsPath + 'ProximaNova-Regular.svg#ProximaNova-Regular') format('svg');
    font-weight: 500;
    font-style: normal;
}

